import React from 'react';
import CssBaseline from '@material-ui/core/CssBaseline';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Link from '@material-ui/core/Link';

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" >
      {'Copyright © '}
      <Link color="inherit" href="https://material-ui.com/">
        Daedalus 
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

const useStyles = makeStyles((theme) => ({
  footer: {
    position: 'fixed',
    bottom: '-1px',
    width: '100%',
    height: '5px',
    opacity: 0.7,
    padding: theme.spacing(3, 2),
    marginTop: 'auto',
    backgroundColor:
      theme.palette.type === 'light' ? theme.palette.grey[200] : theme.palette.grey[800],
  },
}));

export default function Footer() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <footer className={classes.footer}>
        <Container maxWidth="xl">
        <Typography variant="body2" onClick={() => window.scrollTo(0, 110)}  style={{position: 'fixed', left: '520px', bottom: '10px', color: 'red'}}>השאר פרטים</Typography>
        <Typography variant="body2" style={{position: 'fixed', right: '500px', bottom: '10px', color: 'green'}}>אודות דדלוס</Typography>
        <Copyright/>
        </Container>
      </footer>
    </div>
  );
}