import React from 'react';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import axios from 'axios';


export default class MainPage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            first_name: '',
            last_name: '',
            email: '',
            phone: '',
            attorney: ''
        };
    };

    render() {
        console.log(this.state)
        return (
            <div style={{}}>
               <img 
               src="https://pom-tec.com/wp-content/uploads/2019/02/0_5_8_1862058_constructionsite_942146.jpg"
               style={{height: '450px', width: '100%'}}
               /> 
                {/* <img 
               src="logo.jpg"
               style={{height: '150px', width: '30%', position: 'absolute', right: '550px', top: '450px'}}
               />  */}
               {/* <h1 style={{position: 'relative', textAlign: 'center', top: '150px' }}>
                    שיקום זה לא רק לגוף. דדלוס הנדסה מתמחה בביצוע עבודות בחידוש עירוני
             </h1>
             <h1 style={{position: 'relative', textAlign: 'center', top: '150px' }}>
                .בסטנדרטים הגבוהים ביותר של איכות ובטיחות
             </h1>
               <h3 style={{position: 'relative', textAlign: 'center', top: '150px'}}>
            החברה מתמחה בעבודות גמר, מעטפת אחזקת מבנים, איטום ושיקום בטונים וסדקים, ניקוי חלונות, עבודות ריתוך, הרחקת יונים, תוספת מרפסות 
               </h3> */}
               <br />
               <br />
               <form className="form" style={{width: '340px', margin: '0 auto'}}>
                    <TextField
                        autoComplete="first"
                        name="firstName"
                        variant="outlined"
                        required
                        fullWidth
                        id="firstName"
                        label="שם פרטי"
                        onChange={({target: {value}})=> this.onChange(value, 'first_name')}
                    />
                    <TextField
                        variant="outlined"
                        required
                        fullWidth
                        id="lastName"
                        label="שם משפחה"
                        name="lastName"
                        autoComplete="lname"
                        onChange={({target: {value}})=> this.onChange(value, 'last_name')}

                    />
                    <TextField
                        variant="outlined"
                        required
                        fullWidth
                        id="email"
                        label="אימייל"
                        name="email"
                        autoComplete="email"
                        onChange={({target: {value}})=> this.onChange(value, 'email')}

                    />
                    <TextField
                        variant="outlined"
                        required
                        fullWidth
                        name="phone"
                        label="מספר טלפון"
                        type="text"
                        id="phone"
                        autoComplete="phone"
                        onChange={({target: {value}})=> this.onChange(value, 'phone')}
                    />
                    <TextField
                        variant="outlined"
                        required
                        fullWidth
                        name="attorney"
                        label="?הינך מיופה כוח על הנכס"
                        type="text"
                        id="attorney"
                        autoComplete="attorney"
                        onChange={({target: {value}})=> this.onChange(value, 'attorney')}
                    />
                    <br />
                    <br />
                    <Button
                        type="submit"
                        fullWidth
                        variant="contained"
                        color="primary"
                        className="submit"
                        onClick={()=> this.onClick(this.state)}
                    >
                        השאר פרטים
                    </Button>
                </form>
            </div>
        )
    }

    onChange(value, field) {
        this.setState({[field]: value})
    }

    onClick(details) {
        axios.post('https://gentle-caverns-40728.herokuapp.com/sendform', details)
    };



}