import React from "react";
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from 'react-responsive-carousel';

export default function CarouselComponent() {
    return (
        <div className="carousel-wrapper" style={{position: 'relative', top: '20px', paddingBottom: '37px'}}>
            <Carousel autoPlay infiniteLoop useKeyboardArrows showThumbs={false}>
                <div>
                    <img src="https://woodhome.co.il/wp-content/uploads/2019/02/%D7%91%D7%99%D7%AA-%D7%9E%D7%A2%D7%A5-%D7%91%D7%99%D7%AA-%D7%A2%D7%A5-%D7%91%D7%AA%D7%99%D7%9D-%D7%9E%D7%A2%D7%A5-%D7%91%D7%A0%D7%99%D7%99%D7%94-%D7%9E%D7%A2%D7%A5-%D7%91%D7%A0%D7%99%D7%99%D7%94-%D7%A7%D7%9C%D7%94-%D7%91%D7%A0%D7%99%D7%99%D7%94-%D7%99%D7%A8%D7%95%D7%A7%D7%94-e1550647134828.jpg" style={{height: '400px'}} />
                </div>
                <div>
                    <img style={{height: '400px'}} src="https://nadlancz.com/wp-content/uploads/2017/11/%D0%BC%D0%B0%D0%BA%D0%B5%D1%82-%D0%BD%D0%BE%D1%87%D1%8C%D1%8E.jpg" />
                </div>
                <div>
                    <img style={{height: '400px'}} src="https://lh3.googleusercontent.com/proxy/r_HddTiqFpDM6bpty7pQRA1232H3TjLDrN6zB5hhO4m-YP3gXNT9ZFD9csl9B0a0tBXpnnSLUz4i6M9HINRAryp7ZzFTasNr5zPt7WM96og8zE4" />
                </div>
            </Carousel>
            <div style={{backgroundColor: 'lightGrey', paddingTop: '50px'}}>
                <h3>
                    שיקום מבנים מסוכנים מתי מוציאה העירייה צו מבנה מסוכן?
                </h3>
                <p>
                    ניתן להגדיר בניינים רבים ברחבי הארץ כמוזנחים ובלתי מטופלים כיאות. ההזנחה יכולה להיגרם מסיבות שונות
                    <br />
                    בין אם זה כתוצאה מהתנהגות הדיירים, בין כתוצאה מטיפול כושל של וועד הבית, ובין אם זה מסיבות כלכליות שאינן מאפשרות שיפוץ תקופתי של הבניין.
                    <br />
                    בין כך ובין כך, התוצאה הבלתי נלבבת בעליל עבורכם כדיירים היא, בניין מוזנח שאף מסכן את יושביו.
                    <br />
                    את צו מבנה מסוכן מוציאה העירייה ברוב המקרים, רק לאחר שהגיעו מים עד נפש, והמבנה מסכן את דייריו או את העוברים ושבים.
                    <br />

                    למשל: במקרה שבו נופלים מן הבניין משטחים של בטון, וכדומה. 
                    במילים אחרות, כאשר מגיע צו מהעירייה, הדבר מעיד על כך שהבניין מצוי במצב בלתי בטיחותי בעליל.
                    <br />
                </p>

                <h3>
                    מתי יש צורך בשיקום מבנים מסוכנים?
                </h3>

                <p>
                    כאשר העירייה מוציאה צו מבנה מסוכן על בניין מסוים, הדבר מצריך מידית שיקום מבנים מסוכנים.
                <br />
                    צו מבנה מסוכן דורש התייחסות מידית וטיפול הולם, שכן, מעבר לתביעות והקנסות שהוא מטיל, הטיפול המידי בבניין מונע סכנות ממשיות העלולות להיגרם.
                <br />
                    ניתן לראות עדיין, חרף נורת האזהרה של צו מבנה מסוכן, כאלה המנסים לדחות את העניין, אולם התוצאות במקרים כאלה עלולות להיות הרות אסון.
                <br />
                    את השיקום תבצע עבורכם חברת “דדלוס” שיד רב לה בהסרת צווים מסוכנים מעשרות בניינים במרכז.
                </p>
                <h3>
                    שיקום מבנים מסוכנים- מה זה אומר?
                </h3>
                <p>
                    תהליך שיקום מבנים מסוכנים מצריך הליך של בירור היקף הנזקים הקיימים בבניין, וחיזוק של אותם מפגעים.
                    <br />
                    עמודי הבניין ומעטפת הקירות החיצונית של הבניין יהיו המקומות הראשונים הזקוקים לרוב לחיזוק והם מטופלים בראשונה.
                    <br />
                    תהליך זה דורש מומחיות רבה, כמו גם את התערבותו של מהנדס מומחה שיבדוק את הליקויים ויציע תכנית עבודה. 
                    <br />
                    חברת “דדלוס” מעמידה לרשותכם את מיטב אנשי המקצוע מהשורה הראשונה שילוו מקצועית את הפרויקט מתחילתו ועד סופו, ואף ישלימו את עבודת הניירת.
                    <br />
                </p>
                <h3>
                    למה דווקא אנחנו?
                </h3>
                <p>
                “חברת דדלוס הנדסה ויזמות” משרדנו מעניקה שירות בכל אזורי הארץ. החברה עוסקת גם בשיפוץ בניינים, צביעה, ומלווה את הלקוח לאורך כל התהליך.
                <br />
                הניסיון הרב בתחום עם מגוון לקוחות, הופכת את החברה לחברה מובילה בתחומה.
                    חייגו כעת, והבטיחו לעצמכם שירות מהיר, אדיב ומקצועי.
                </p>
            </div>
        </div>
    );
}